@use 'base/variables' as v;
@use 'base/mixins' as m;

.site-footer{
    background-color: v.$color1;
    padding: 1rem;
    p{
        text-align: center;
        color: v.$blanco;
    }
    .iconos-footer{
        display: flex;
        justify-content: center;
        gap: 10rem;
        margin: 2rem 0;
        .icon{
            background-color: v.$blanco;
            width: 4rem;
            height: 4rem;
            border-radius: 100%;
        }
    }
}