@use 'base/variables' as v;
@use 'base/mixins' as m;

.centrar-texto{
    text-align: center;
    margin: 0;
    padding: 4rem;
}
.contacto-bg {
    background-image: url(../img/contacto.jpg);
    height: 40rem;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    border-radius: 2rem;
}

.formulario {
    background-color: v.$blanco;
    margin: -5rem auto 0 auto;
    width: 95%;
    padding: 5rem;
    border-radius: 2rem;
    .campo {
        margin-bottom: 2rem;
        .campo__label {
            flex: 0 0 9rem;
            text-align: right;
        }
        .campo__field {
            flex: 1;
            border: 1px solid v.$gris;
            width: 100%;
            border-radius: 2rem;
            padding: 1rem;
            margin-top: 2rem;
        }
        .campo__field--textarea {
            height: 20rem;
        }
    }
    .centrar{
        display: flex;
        justify-content: center;
    }
}