@use 'base/variables' as v;
@use 'base/mixins' as m;

.proyectos{
    padding: 5rem 0;
    h3{
        text-align: center;
    }
}

.proyectos-imagenes{
    @include m.resetear-lista;
    @include m.grid(2, 2rem);
    @include m.tablet{
        @include m.grid(4, 2rem)
    }
    @include m.desktop{
        @include m.grid(4, 5rem)
    }
}

.grid-p{
    @include m.tablet{
        @include m.grid(4, 2rem)
    }
    @include m.desktop{
        @include m.grid(4, 5rem)
    }
    ul{
        @include m.resetear-lista;
        -webkit-box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.75);
        -moz-box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.75);
        box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.75)
    }
    li{
        padding: 2.5rem;
        font-weight: 700;
        text-align: center;
        a{
            color:v.$color1;
            &:hover{
                color: v.$rosa;
            }
        }
    }
    li:nth-child(1){
        background-color: v.$color3;
        text-align: center;
        text-transform: uppercase;
        color: v.$blanco;
    }
    li:nth-child(2){
        padding: 0;
    }
    li:nth-child(2), li:nth-child(4){
        border-bottom: 1px solid #e1e1e1;
    }
    li:nth-child(4), li:nth-child(6){
        font-weight: normal;
        text-align: center;
    }
}

.overlay{
    position: fixed; 
    right: 0;
    left: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    display: grid;
    place-items: center;
    background-color: rgb(0 0 0 / .5); 
    picture{
        display: block;
        max-width: 80rem;
    }
}

.boton-cerrar{
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: v.$blanco;
    color: v.$negro;
    font-weight: 700;
    display: grid;
    place-items: center;
    &:hover{
        cursor: pointer;
    }
}

.imagenes:hover{
    cursor: pointer;
}