// Importar variables
@use 'variables' as v;
// Importar mixins
@use 'base/mixins' as m;

// Globales
html {
    font-size: 62.5%; // 1rem = 10px
    box-sizing: border-box; // Solucionar problemas con el padding
}
*, 
*:before,
*:after {
    box-sizing: inherit;
}

body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    .ws{
        z-index: 6;
        position: fixed;
        right: 10px;
        bottom: 10px;
        width: 8rem;
        height: 8rem;
    }
    &.fijar{
        overflow: hidden;
    }
    &.body-scroll{
        padding-top: 8.6rem;
    }
}

p {
    font-size: 2rem;
    color: v.$negro;
    line-height: 1.5;
}

.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

h1, h2, h3 {
    margin: 0 0 5rem 0;
}
h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
}
h4{
    font-size: 2rem;
}

img {
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}