@use 'base/variables' as v;
@use 'base/mixins' as m;

.estudios{
    padding: 5rem 0;
    background-color: v.$color2;
    h3{
        text-align: center;
        color: v.$blanco;
        margin-bottom: 4rem;
    }
    h4{
        text-align: center;
        color: v.$blanco;
        margin: 6rem 0;
    }
    .contenedor-educacion{
        @include m.tablet{
            @include m.grid(2, 5rem);
        }
    }
}

.curso{
    margin-bottom: 2rem;
    &-header, &-footer{
        p{
            text-align: center;
            padding: .5rem;
            margin: 0;
        }
    }
    &-header{
        background-color: v.$color3;
        p{
            color: v.$blanco;
            text-transform: uppercase;
            font-size: 2rem;
            font-weight: bold;
        }
    }
    &-body{
        background-color: v.$blanco;
        ul{
            @include m.resetear-lista();
            li, :nth-child(odd){
                background-color: v.$blanco;
                padding: 1rem;
                border-bottom: 1px solid #e1e1e1;
            }
        }
    }
    &-footer{
        background-color: v.$color4;
        p{
            font-size: 2.4rem;
            font-weight: 700;
        }
    }
}

.idiomas{
    display: flex;
    justify-content: center;
    .idioma{
        width: 8rem;
        height: 8rem;
    }
}

.habilidades-imagenes{
    @include m.resetear-lista;
    @include m.grid(3, 2rem);
    @include m.desktop{
        @include m.grid(5, 4rem)
    }
    li{
        display: flex;
        flex-direction: column;
        align-items: center;
        p{
            text-align: center;
            color: v.$blanco;
            font-weight: 700;
            font-size: 1.5rem;
        }
    }
    .hab{
        width: 6rem;
        height: 6rem;
    }
}