@use 'base/variables' as v;
@use 'base/mixins' as m;

.experiencia{
    background-color: v.$color2;
    padding: 5rem 0;
    h3{
        text-align: center;
        color: v.$blanco;
    }
}

.exp-contenedor{
    @include m.tablet {
        @include m.grid(2, 5rem);
        .exp:nth-child(3){
            grid-column: 1/3;
        }
    }
}

.exp{
    padding-top: 2rem;
    margin-bottom: 2rem;
    p{
        text-align: center;
        font-weight: bold;
        text-transform: uppercase;
    }
    .nombre-exp:after{
            content: "";
            display: block;
            width: 20rem;
            height: 10rem;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center center;
            margin: 2rem auto 0 auto;
    }
}

.pdvsa .nombre-exp:after{
    background-image: url(../img/pdvsa.png);
}

.workana .nombre-exp:after{
    background-image: url(../img/workana.png);
}

.background{
    background-color: v.$blanco;
    li:nth-child(1){
        background-color: v.$color3;
        color: v.$blanco;
    }
    li:nth-child(3){
        background-color: v.$color4;
    }
}

.descripcion{
    @include m.resetear-lista;
    li{
        padding: 2.5rem;
        font-weight: 700;
        span{
            font-weight: 400;
            font-size: 1.7rem;
        }
    }
}