@use 'variables' as v;

// Media Querie
@mixin telefono {
    @media (min-width: v.$telefono) { 
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) { 
        @content;
    }
}

@mixin maxTablet {
    @media (max-width: v.$mTablet) { 
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop) { 
        @content;
    }
}

@mixin desktopXL {
    @media (min-width: v.$desktopXL) {
        @content;
    }
}

// Display Grid
@mixin grid($columnas, $espaciado) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espaciado;
}

// Reseteo de lista y quitar viñetas
@mixin resetear-lista{
    margin: 0;
    padding: 0;
    list-style: none;
}