@use 'base/variables' as v;
@use 'base/mixins' as m;

.video {
    position: relative;
    z-index: 1;
    max-height: 70rem;
    min-height: 50rem;
    overflow: hidden;
    .overlay {
        z-index: 2;
        position: absolute;
        background: rgb(0,0,0);
        background: linear-gradient(90deg, rgba(0,0,0,0.5) 0%, rgba(87,8,222,0.3309698879551821) 100%);
        height: 100%;
        width: 100%;
    }
    video {
        width: auto;
        display: block;
        @include m.desktop {
            width: 100%;
        }
    }
}

.contenido-video {
    z-index: 3;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    h2 {
        color: v.$blanco;
        font-size: 7rem;
        margin: 0;
        text-align: center;
    }
    p {
        padding: 1rem 2rem;
        color: v.$blanco;
        font-weight: 700;
        font-size: 2.4rem;
        margin: 0;
    }
}

.icono{
    display: flex;
    align-items: flex-end;
    margin-top: 1.5rem;
}

.boton-cont{
    background-color: v.$color2;
    color: v.$blanco;
    padding: 1rem 3rem;
    margin-top: 3.5rem;
    font-size: 2rem;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: bold;
    border-radius: .5rem;
    width: 90%;
    text-align: center;
    border: none;
    @include m.tablet{
        width: auto;
    }
    &:hover{
        cursor: pointer;
    }
}