@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
  background-color: v.$color1;
  z-index: 5;
  &.fijar-barra{
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: .3rem .3rem .3rem v.$grisx;
  }
  .contenido-header {
    padding: 2rem;
    @include m.tablet{
      display: flex;
      justify-content:space-between;
      align-items: center;
    }
  }
}

.navbar{
  @include m.maxTablet{
    display: flex;
    justify-content: space-between;
  }
  h1 {
    color: v.$blanco;
    text-align: center;
    @include m.tablet {
        margin: 0;
    }
    @include m.maxTablet{
      margin: .5rem 0 0 0;
      font-size: 3.5rem;
    }
  }
  .ham{
    background-color: transparent;
    border: none;
    padding: 0;
    color: v.$blanco;
    font-size: 2.5rem;
    display: none;
    cursor: pointer;
    &:hover{
      color: v.$color3;
      transition: color .7s;
    }
    @include m.maxTablet{
      display: flex;
      margin-top: 1.3rem;
    }      
  } 
}

.menu {
  list-style: none;
  padding: 0;
  &.visible{
    left: 0;
  }
  @include m.maxTablet{
    z-index: 4;
    background-color: v.$color3;
    box-shadow: .3rem .3rem .3rem v.$grisx;
    position: fixed;
    top: 68px;
    left: 0;
    width: 100%;
    left: 100%;
    transition: left 1s;
  }
  @include m.tablet {
    display: flex;
    gap: 2rem;
  }
  a {
    display: block;
    text-align: center;
    color: v.$blanco;
    font-size: 2.4rem;
    margin-bottom: 1rem;
    @include m.tablet {
        margin: 0;
    }
    @include m.maxTablet{
      padding-top: 1.5rem
    }
    &:hover {
        color: v.$color3;
        transition: color .7s;
        @include m.maxTablet{
          color: v.$color1;
        }
    }
  }
}