html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body .ws {
  z-index: 6;
  position: fixed;
  right: 10px;
  bottom: 10px;
  width: 8rem;
  height: 8rem;
}
body.fijar {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: #21094E;
  z-index: 5;
}
.header.fijar-barra {
  left: 0;
  top: 0;
  position: fixed;
  width: 100%;
  box-shadow: 0.3rem 0.3rem 0.3rem #333;
}
.header .contenido-header {
  padding: 2rem;
}
@media (min-width: 820px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (max-width: 819px) {
  .navbar {
    display: flex;
    justify-content: space-between;
  }
}
.navbar h1 {
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 820px) {
  .navbar h1 {
    margin: 0;
  }
}
@media (max-width: 819px) {
  .navbar h1 {
    margin: 0.5rem 0 0 0;
    font-size: 3.5rem;
  }
}
.navbar .ham {
  background-color: transparent;
  border: none;
  padding: 0;
  color: #FFFFFF;
  font-size: 2.5rem;
  display: none;
  cursor: pointer;
}
.navbar .ham:hover {
  color: #4CA1A3;
  transition: color 0.7s;
}
@media (max-width: 819px) {
  .navbar .ham {
    display: flex;
    margin-top: 1.3rem;
  }
}

.menu {
  list-style: none;
  padding: 0;
}
.menu.visible {
  left: 0;
}
@media (max-width: 819px) {
  .menu {
    z-index: 4;
    background-color: #4CA1A3;
    box-shadow: 0.3rem 0.3rem 0.3rem #333;
    position: fixed;
    top: 68px;
    left: 0;
    width: 100%;
    left: 100%;
    transition: left 1s;
  }
}
@media (min-width: 820px) {
  .menu {
    display: flex;
    gap: 2rem;
  }
}
.menu a {
  display: block;
  text-align: center;
  color: #FFFFFF;
  font-size: 2.4rem;
  margin-bottom: 1rem;
}
@media (min-width: 820px) {
  .menu a {
    margin: 0;
  }
}
@media (max-width: 819px) {
  .menu a {
    padding-top: 1.5rem;
  }
}
.menu a:hover {
  color: #4CA1A3;
  transition: color 0.7s;
}
@media (max-width: 819px) {
  .menu a:hover {
    color: #21094E;
  }
}

.video {
  position: relative;
  z-index: 1;
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;
}
.video .overlay {
  z-index: 2;
  position: absolute;
  background: rgb(0, 0, 0);
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.5) 0%, rgba(87, 8, 222, 0.330969888) 100%);
  height: 100%;
  width: 100%;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  z-index: 3;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contenido-video h2 {
  color: #FFFFFF;
  font-size: 7rem;
  margin: 0;
  text-align: center;
}
.contenido-video p {
  padding: 1rem 2rem;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 2.4rem;
  margin: 0;
}

.icono {
  display: flex;
  align-items: flex-end;
  margin-top: 1.5rem;
}

.boton-cont {
  background-color: #511281;
  color: #FFFFFF;
  padding: 1rem 3rem;
  margin-top: 3.5rem;
  font-size: 2rem;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  border-radius: 0.5rem;
  width: 90%;
  text-align: center;
  border: none;
}
@media (min-width: 820px) {
  .boton-cont {
    width: auto;
  }
}
.boton-cont:hover {
  cursor: pointer;
}

.sobre-mi {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5rem 0;
}
@media (min-width: 480px) {
  .sobre-mi .imagen {
    display: flex;
    justify-content: center;
  }
}
@media (min-width: 820px) {
  .sobre-mi {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
    place-items: center;
  }
}

.mi-contenido {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 820px) {
  .mi-contenido {
    margin-top: 0;
  }
}
.mi-contenido h2 {
  margin-bottom: 2rem;
  text-align: center;
}
@media (min-width: 820px) {
  .mi-contenido h2 {
    text-align: left;
  }
}
.mi-contenido .titulo {
  color: #F53756;
  font-weight: 700;
  text-align: center;
}
@media (min-width: 820px) {
  .mi-contenido .titulo {
    text-align: left;
  }
}

.bordes {
  height: 40rem;
  width: 30rem;
  border-radius: 5rem;
  -webkit-box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.75);
}

.experiencia {
  background-color: #511281;
  padding: 5rem 0;
}
.experiencia h3 {
  text-align: center;
  color: #FFFFFF;
}

@media (min-width: 820px) {
  .exp-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
  .exp-contenedor .exp:nth-child(3) {
    grid-column: 1/3;
  }
}

.exp {
  padding-top: 2rem;
  margin-bottom: 2rem;
}
.exp p {
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
}
.exp .nombre-exp:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

.pdvsa .nombre-exp:after {
  background-image: url(../img/pdvsa.png);
}

.workana .nombre-exp:after {
  background-image: url(../img/workana.png);
}

.background {
  background-color: #FFFFFF;
}
.background li:nth-child(1) {
  background-color: #4CA1A3;
  color: #FFFFFF;
}
.background li:nth-child(3) {
  background-color: #A5E1AD;
}

.descripcion {
  margin: 0;
  padding: 0;
  list-style: none;
}
.descripcion li {
  padding: 2.5rem;
  font-weight: 700;
}
.descripcion li span {
  font-weight: 400;
  font-size: 1.7rem;
}

.proyectos {
  padding: 5rem 0;
}
.proyectos h3 {
  text-align: center;
}

.proyectos-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 820px) {
  .proyectos-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .proyectos-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 5rem;
  }
}

@media (min-width: 820px) {
  .grid-p {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .grid-p {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 5rem;
  }
}
.grid-p ul {
  margin: 0;
  padding: 0;
  list-style: none;
  -webkit-box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.75);
}
.grid-p li {
  padding: 2.5rem;
  font-weight: 700;
  text-align: center;
}
.grid-p li a {
  color: #21094E;
}
.grid-p li a:hover {
  color: #F53756;
}
.grid-p li:nth-child(1) {
  background-color: #4CA1A3;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
}
.grid-p li:nth-child(2) {
  padding: 0;
}
.grid-p li:nth-child(2), .grid-p li:nth-child(4) {
  border-bottom: 1px solid #e1e1e1;
}
.grid-p li:nth-child(4), .grid-p li:nth-child(6) {
  font-weight: normal;
  text-align: center;
}

.overlay {
  position: fixed;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.boton-cerrar {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: #FFFFFF;
  color: #000000;
  font-weight: 700;
  display: grid;
  place-items: center;
}
.boton-cerrar:hover {
  cursor: pointer;
}

.imagenes:hover {
  cursor: pointer;
}

.estudios {
  padding: 5rem 0;
  background-color: #511281;
}
.estudios h3 {
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 4rem;
}
.estudios h4 {
  text-align: center;
  color: #FFFFFF;
  margin: 6rem 0;
}
@media (min-width: 820px) {
  .estudios .contenedor-educacion {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.curso {
  margin-bottom: 2rem;
}
.curso-header p, .curso-footer p {
  text-align: center;
  padding: 0.5rem;
  margin: 0;
}
.curso-header {
  background-color: #4CA1A3;
}
.curso-header p {
  color: #FFFFFF;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: bold;
}
.curso-body {
  background-color: #FFFFFF;
}
.curso-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.curso-body ul li, .curso-body ul :nth-child(odd) {
  background-color: #FFFFFF;
  padding: 1rem;
  border-bottom: 1px solid #e1e1e1;
}
.curso-footer {
  background-color: #A5E1AD;
}
.curso-footer p {
  font-size: 2.4rem;
  font-weight: 700;
}

.idiomas {
  display: flex;
  justify-content: center;
}
.idiomas .idioma {
  width: 8rem;
  height: 8rem;
}

.habilidades-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}
@media (min-width: 992px) {
  .habilidades-imagenes {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 4rem;
  }
}
.habilidades-imagenes li {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.habilidades-imagenes li p {
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 1.5rem;
}
.habilidades-imagenes .hab {
  width: 6rem;
  height: 6rem;
}

.site-footer {
  background-color: #21094E;
  padding: 1rem;
}
.site-footer p {
  text-align: center;
  color: #FFFFFF;
}
.site-footer .iconos-footer {
  display: flex;
  justify-content: center;
  gap: 10rem;
  margin: 2rem 0;
}
.site-footer .iconos-footer .icon {
  background-color: #FFFFFF;
  width: 4rem;
  height: 4rem;
  border-radius: 100%;
}

.centrar-texto {
  text-align: center;
  margin: 0;
  padding: 4rem;
}

.contacto-bg {
  background-image: url(../img/contacto.jpg);
  height: 40rem;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  border-radius: 2rem;
}

.formulario {
  background-color: #FFFFFF;
  margin: -5rem auto 0 auto;
  width: 95%;
  padding: 5rem;
  border-radius: 2rem;
}
.formulario .campo {
  margin-bottom: 2rem;
}
.formulario .campo .campo__label {
  flex: 0 0 9rem;
  text-align: right;
}
.formulario .campo .campo__field {
  flex: 1;
  border: 1px solid #e1e1e1;
  width: 100%;
  border-radius: 2rem;
  padding: 1rem;
  margin-top: 2rem;
}
.formulario .campo .campo__field--textarea {
  height: 20rem;
}
.formulario .centrar {
  display: flex;
  justify-content: center;
}