@use 'base/variables' as v;
@use 'base/mixins' as m;

.sobre-mi {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5rem 0;
    @include m.telefono{
        .imagen{
            display: flex;
            justify-content: center;
        }
    }
    @include m.tablet {
        @include m.grid(2, 1rem);
        place-items: center;
    }
}

.mi-contenido {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 5rem;
    @include m.tablet {
        margin-top:0;
    }
    h2 {
        margin-bottom: 2rem;
        text-align: center;
        @include m.tablet{
            text-align: left;
        }
    }
    .titulo {
        color: v.$rosa;
        font-weight: 700;
        text-align: center;
        @include m.tablet{
            text-align: left;
        }
    }
}

.bordes{
    height: 40rem;
    width: 30rem;
    border-radius: 5rem;
    -webkit-box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.75)
}